
.inspector-popup {
    position: absolute;
    top: 47px;
    left: 52px;
    right: 52px;
    height: 248px;

    max-width: 800px;
    margin: auto;

    padding: 25px;

    background-color: #FFF;
    color: #707070;
    border: 1px solid #DADADA;

    display: flex;
    align-items: center;

    animation: slide-out .4s ease;
    
    &--green {
        color: #146FF0;
        cursor: default;
    }
    &__wrapper {
        position: fixed;
        left: 0; top: 0;
        bottom: 0;
        width: 100%;

        background-color: rgba(51, 51, 51, 0.27);
        overflow: hidden; 

        opacity: 0;
        pointer-events: none;

        transition: opacity .3s ease;

        &--visible {
            opacity: 1;
            pointer-events: all;
            .inspector-popup {
                animation: slide-in .4s ease;
            }
        }
    }
    &__close {
        display: block;
        position: absolute;
        right: 25px;
        top: 25px;

        width: 16px;
        height: 16px;

        background-image: url('./src/close.png');
        background-repeat: no-repeat;
        background-position: center;
        background-size: 100%;
        cursor: pointer;
        &:hover {
            transform: scale(1.1);
        }
    }
    &__qr {
        width: 198px;
        min-width: 198px;
        height: 198px;
        min-height: 198px;
        margin-right: 30px;
        
        background-position: center;
        background-repeat: no-repeat;
        background-size: 115%;
    }
    &__section {
        width: 100%;
    }
    &__header {
        font-size: 16px;
        font-weight: 400;
        line-height: 18px;
        margin-bottom: 8px;
    }
    &__list {
        list-style: none;
        font-size: 14px;
        & a {
            color: #0066D3;
            cursor: pointer;
            user-select: none;
            text-decoration: none;
            &:hover {
                text-decoration: underline !important;
            }
        }
    }
    &__paragraph {
        font-size: 14px;
        margin-top: 23px;
    }
}

@keyframes slide-in {
    0% {
        transform: translateY(-30px);
    }
    100% {
        transform: translateY(0);
    }
}

@keyframes slide-out {
    0% {
        transform: translateY(0px);
    }
    100% {
        transform: translateY(-30px);
    }
}

@media only screen and (max-width: 770px) {
    .inspector-popup {
        height: auto;
        flex-direction: column;
        align-items: flex-start;
        min-width: 286px;
        &__qr {
            margin-bottom: 18px;
        }
    }
}
